import shimWorker from 'rollup-plugin-bundle-worker';
export default new shimWorker("./tickworker.js", function (window, document) {
var self = this;

var interval;
self.addEventListener('message', function(e){
    let msg = e.data.msg
    switch (msg) {
        case 'start':
            clearInterval(interval);
            interval = setInterval(function(){
                self.postMessage('tick');
            }, e.data.interval);
            break;
        case 'stop':
            clearInterval(interval);
            break;
    };
}, false);

});